import React from "react";
import { useState, Fragment } from "react"
import { storiesOf } from "@storybook/react"

import Modal from "../components/Modal";

const stories = storiesOf("Example", module).addParameters({ storySource: { source: __STORY__, locationsMap: __LOCATIONS_MAP__ } });

stories.add("App", () => {
    const [active, setActive] = useState(false)

    const close = () => {
      setActive(!active)
    }
  
    return (
      <Fragment>
        <div className = "container">
          <h1>React Modal</h1>
          <h2>Classique</h2>
          <button onClick = {() => setActive(true)}>Save</button>
          <Modal
              open = {active}
              close = {close}
          />

        <h2>With close button customized</h2>
          <button onClick = {() => setActive(true)}>Save</button>
          <Modal
              open = {active}
              close = {close}
          />
        </div>
      </Fragment>
  
    )
});